<template>
  <div class="grid place-content-center">
    <div class="mt-24 space-y-5">
      <div
        @click="upcommingMessage"
        class="flex items-center p-4 transition-all duration-300 transform bg-gray-100 rounded shadow-md cursor-pointer  hover:shadow-lg ease hover:scale-105 hover:bg-gray-200"
      >
        <img
          src="https://img.icons8.com/external-flatart-icons-lineal-color-flatarticons/64/000000/external-call-communication-and-media-flatart-icons-lineal-color-flatarticons.png"
          class="h-16"
        />
        <h2 class="ml-2 text-xl font-bold">Free Consultency Call</h2>
      </div>
      <div @click="upcommingMessage"
        class="flex items-center p-4 transition-all duration-300 transform bg-gray-100 rounded shadow-md  hover:shadow-lg ease hover:scale-105 hover:bg-gray-200"
      >
        <img
          src="https://img.icons8.com/external-flatart-icons-lineal-color-flatarticons/50/000000/external-meeting-digital-marketing-flatart-icons-lineal-color-flatarticons.png"
          class="h-16"
        />
        <h2 class="ml-2 text-xl font-bold">Special Advisory Meeting</h2>
      </div>
      <div
        @click="upcommingMessage"
        class="flex items-center p-4 transition-all duration-300 transform bg-gray-100 rounded shadow-md cursor-pointer  hover:shadow-lg ease hover:scale-105 hover:bg-gray-200"
      >
        <img
          src="https://img.icons8.com/external-vitaliy-gorbachev-lineal-color-vitaly-gorbachev/60/000000/external-chat-online-shopping-vitaliy-gorbachev-lineal-color-vitaly-gorbachev.png"
          class="h-16"
        />
        <h2 class="ml-2 text-xl font-bold">Problem Solving (Chat)</h2>
      </div>
    </div>
    <Modal title="Free Consultency Call">
      <template v-slot:body>
        <div v-if="team">
          <Support :team="team" />
        </div>
      </template>
    </Modal>
  </div>
</template>

<script>
import Modal from "@/components/Modal";
import Support from "@/views/Support";
import { mapMutations } from "vuex";
export default {
  components: { Modal, Support },
  data() {
    return {
      team: null,
    };
  },
  methods: {
    ...mapMutations(["toggleModal"]),

    async supportTeam() {
      const res = await fetch(
        "https://sheets.googleapis.com/v4/spreadsheets/1VRON9WcyjdXk1bJlJ3t93aOABMUzSJsHFLGApl-c2bg/values/consultent?key=AIzaSyDdZJsxR25GrYzvtTHsxFGYaalGkKIUaXM"
      );
      const data = await res.json();
      this.team = data.values.splice(1).map((d) => ({
        pic: `https://drive.google.com/uc?id=${d[0].substr(32, 33)}`,
        name: d[1],
        des: d[2],
        phone: d[3],
      }));
    },
    upcommingMessage() {
      this.$swal({
        icon: "warning",
        title: "Upcoming",
        text: "Wait untill the service is available.",
      });
    },
  },
  created() {
    this.supportTeam();
  },
};
</script>

